import '../../assets/styles/pages/joinUs.css';
import { Form, Input, Button, DatePicker, Select, Upload,} from 'antd';
import { UploadOutlined, LeftCircleFilled } from '@ant-design/icons';
import { Link } from "react-router-dom";
import videoFondo from '../../assets/images/medumedusin.mp4';
import React, {useState} from 'react';
import dayjs from 'dayjs';

const { Option } = Select;
const { TextArea } = Input;
const empleos = [
    "Abogado", "Ingeniero en Sistemas", "Mdico", "Arquitecto", "Profesor",
    "Enfermero", "Psiclogo", "Contador", "Qumico", "Bilogo", "Economista",
    "Abogado Penalista", "Ingeniero Civil", "Diseador Grfico", "Programador Web",
    "Desarrollador de Software", "Analista de Sistemas", "Investigador", 
    "Marketing Digital", "Odontlogo", "Veterinario", "Farmacutico", 
    "Ingeniero Elctrico", "Ingeniero Mecnico", "Piloto", "Administrador de Empresas", 
    "Tcnico en Electrnica", "Chef", "Cocinero", "Mesero", "Barbero", 
    "Estilista", "Mecnico Automotriz", "Electricista", "Plomero", "Carpintero", 
    "Albail", "Pintor", "Cerrajero", "Ingeniero Qumico", "Fotgrafo", 
    "Periodista", "Redactor", "Traductor", "Intrprete", "Fisioterapeuta", 
    "Nutricionista", "Entrenador Personal", "Soldador", "Jardinero", 
    "Guardia de Seguridad", "Bombero", "Polica", "Militar", "Banquero", 
    "Agente de Seguros", "Corredor de Bolsa", "Consultor", "Asesor Financiero", 
    "Gestor de Proyectos", "Gerente de Ventas", "Desarrollador de Videojuegos", 
    "Analista de Datos", "Ingeniero de Redes", "Especialista en Ciberseguridad", 
    "Terapeuta", "Piloto de Drones", "Agrnomo", "Msico", "Artista Plstico"
].sort();

export default function JoinUs() {
    const [filtroEmpleo, setFiltroEmpleo] = useState('');
    const [imagenPresentacion, setImagenPresentacion] = useState(null); 
    const [mensajeCV, setMensajeCV] = useState('');

    const manejarBusqueda = (value) => {
        setFiltroEmpleo(value);
    };

    const disableFutureDates = (current) => {
        const today = dayjs();
        const yearsAgo = today.subtract(18, 'year');
        return current && (current.isAfter(today) || current.isSame(today, 'day') || current.isAfter(yearsAgo));
    };
    
    const empleosFiltrados = empleos.filter((empleo) =>
        empleo.toLowerCase().includes(filtroEmpleo.toLowerCase())
    );

    const manejarFotoSubida = (info) => {
        if (info.fileList.length > 0) {
            setImagenPresentacion(info.fileList[0].originFileObj); 
        } else {
            setImagenPresentacion(null); 
        }
        return info.fileList;
    };

    const manejarCVSubido = (info) => {
        if (info.fileList.length > 0) {
            setMensajeCV('Nuestros administradores revisarn tu currculum.');
        } else {
            setMensajeCV(''); 
        }
        return info.fileList;
    };

    return (

    <div className='JoinUs-page-contein'>
        <video autoPlay loop muted className='full-screen-background'>
            <source src={videoFondo} type="video/mp4" />
            Tu navegador no soporta el formato de video.
        </video>
        <Link Link to="/" className="back-arrow">
            <LeftCircleFilled />
        </Link>
        <div className='contain-form'>
            <Form >
                <h1 className='form-title'>Registrate</h1>
                <div className='form-row'>
                <Form.Item
                    className='form-items-joinUs'
                    name="nombre"
                    rules={[
                    {
                        type: 'string',
                        message: 'Por favor, ingresa un nombre.',
                    },
                    {
                        required: true,
                        message: 'Por favor, ingresa tu nombre.',
                    },
                    ]}
                >
                    <Input placeholder="Nombre"/>
                </Form.Item>
                <Form.Item
                    className='form-items-joinUs'
                    name="apellido"
                    rules={[
                    {
                        type: 'string',
                        message: 'Por favor, ingresa un apellido.',
                    },
                    {
                        required: true,
                        message: 'Por favor, ingresa tu apellido.',
                    },
                    ]}
                >
                    <Input placeholder="Apellido" />
                </Form.Item>
                 {/* DNI */}
                <Form.Item
                    className='form-items-joinUs'
                    name="dni"
                    rules={[
                    {
                        required: true,
                        message: 'Por favor, ingresa tu DNI.',
                    },
                    ]}
                >
                    <Input placeholder="DNI"/>
                </Form.Item>
                </div>

                <div className='form-row'>
                {/* Email */}
                <Form.Item
                    className='form-items-joinUs'
                    name="email"
                    rules={[
                    {
                        type: 'email',
                        message: 'Por favor, ingresa un email vlido.',
                    },
                    {
                        required: true,
                        message: 'Por favor, ingresa tu email.',
                    },
                    ]}
                >
                    <Input placeholder="Email"/>
                </Form.Item>
                {/* Nmero de telfono */}
                <Form.Item
                    className='form-items-joinUs'
                    name="telefono"
                    rules={[
                    {
                        required: true,
                        message: 'Por favor, ingresa tu nmero de telfono.',
                    },
                    {
                        pattern: new RegExp(/^[0-9]+$/),
                        message: 'Solo se permiten nmeros.',
                    },
                    ]}
                >
                    <Input placeholder="Telfono"/>
                </Form.Item>
                {/* Localidad */}
                <Form.Item
                    className='form-items-joinUs'
                    name="localidad"
                    rules={[
                    {
                        required: true,
                        message: 'Por favor, ingresa el nombre de tu localidad.',
                    },
                    ]}
                >
                    <Input placeholder="Localidad"/>
                </Form.Item>
                </div>
            <div className="form-row">
                {/* Fecha de Nacimiento */}
                <div className="birth-date-container">
                <Form.Item
                    className='birthDate'
                    name="fechaNacimiento"
                    label={<span style={{color: 'white'}}>Fecha de Nacimiento</span>}
                    rules={[{ required: true, message: 'Por favor, selecciona tu fecha de nacimiento.' }]}
                >
                    <DatePicker
                        className='date-picker-custom'
                        placeholder="Selecciona la fecha"
                        disabledDate={disableFutureDates}> // Aqu se aplica la funcin
                    </DatePicker>
                </Form.Item>
                </div>
            </div>

        <h2 className='form-subtitle'>Cuntanos sobre ti</h2>
                {/* Seleccin de Empleo */}
                <Form.Item
                    name="empleo"
                    rules={[
                        {
                            required: true,
                            message: 'Por favor, selecciona tu empleo.',
                        },
                    ]}
                >
                    <Select
                        placeholder="Selecciona tu empleo"
                        onSearch={manejarBusqueda}
                        showSearch
                        filterOption={false}
                    >
                        {empleosFiltrados.map((empleo) => (
                            <Option key={empleo} value={empleo}>
                                {empleo}
                            </Option>
                        ))}
                    </Select>
                </Form.Item>
               {/* Descripcin */}
               <Form.Item
                    name="descripcion"
                    rules={[
                    {
                        required: true,
                        message: 'Por favor, ingresa una descripcin.',
                    },
                    ]}
                >
            
            <TextArea placeholder='Descripcin' rows={4} style={{ resize: 'none', minHeight: '120px', backgroundColor: 'transparent' }} />
        </Form.Item>

                {/* Subir Archivo de Foto de Presentacin */}
                <Form.Item
                    name="foto-presentacion"
                    valuePropName="fileList"
                    getValueFromEvent={manejarFotoSubida}
                    rules={[
                        {
                            required: true,
                            message: 'Por favor, ingresa una foto de presentacin.',
                        },
                    ]}
                >
                    <Upload name="archivo" listType="picture" beforeUpload={() => false}>
                        <Button icon={<UploadOutlined />}>Subir foto de presentacin</Button>
                    </Upload>
                </Form.Item>

                {/* Vista previa de la foto de presentacin */}
                {imagenPresentacion && (
                    <div className="imagen-preview">
                        <img
                            src={URL.createObjectURL(imagenPresentacion)}
                            alt="Vista previa"
                            className="foto-preview"
                        />
                        <p>Esta ser tu foto de presentacin. Asegura que te veas bien!</p>
                    </div>
                )}

                {/* Subir CV */}
                <Form.Item
                    name="curriculum-vitae"
                    valuePropName="fileList"
                    getValueFromEvent={manejarCVSubido}
                    rules={[
                        {
                            required: true,
                            message: 'Por favor, ingresa su Currculum Vitae.',
                        },
                    ]}
                >
                    <Upload name="archivo" listType="picture" beforeUpload={() => false}>
                        <Button icon={<UploadOutlined />}>Subir Curriculum</Button>
                    </Upload>
                </Form.Item>
                {mensajeCV && <p style={{ color: 'white' }}>{mensajeCV}</p>}
        

        {/* Botn de enviar */}
        <Form.Item wrapperCol={{ offset: 8, span: 16 }} className='buttom-item'>
            <Button type="submit" id="joinUsButton">Enviar</Button>
        </Form.Item>
                
            </Form>
        </div>
    </div>

    )

}